.search-box {
  display: flex;
  align-items: center;
  padding: .5rem 0;
  position: fixed;
  width: 25vw;
  top: 0;
  background-color: #fff;

  .search-input {
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    .search-icon {
      position: absolute;
      left: .5rem;
      width: 3rem;
      .magnifier {
        width: 100%;
        fill: #7f8f9e;
      }
    }
    .search-twitter {
      padding: .8rem;
      padding-left: 4rem;
      font-size: 1rem;
      color: #8998a6;
      width: 100%;
      border-radius: 10000px;
      background-color: #f7f9fa;
      border: none;
      outline: none;
    }
  }


}

