.trending {
  background-color: #f7f9fa;
  border-radius: 1rem;
  width: 100%;
  // margin-top: 1.2rem;
  margin-top: 5rem;
  padding-bottom: 1rem;
  .trending-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    border-bottom: 1px solid rgb(235, 238, 240);
    p {
      font-weight: 700;
      font-size: 1.2rem;
    }

    .icon-holder {
      width: 3rem;

      .icon-cog {
        width: 100%;
        fill: #7f8f9e;
      }
    }

  }
  .trends {
    padding: 1rem;
    border-bottom: 1px solid rgb(235, 238, 240);
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    &:hover {
      background-color: #ebeef0;
      transition: .3s ease;
      cursor: pointer;
    }

    .trend-details {

      span {
        display: inline-block;
        color: #718394;
        font-size: .9rem;
      }
      .trend-title {
        color: #0f1419;
        font-weight: 600;
        font-size: 1.2rem;
      }
      .trend-tweets {
        color: #718394;
        font-size: .9rem;
      }
    }
    .more-icons {
      margin-top: -5px;
      font-weight: 600;
      font-size: 1.2rem;
    }
  }
}