.home-wrapper {
  display:  grid;
  grid-template-columns: [left-start] .4fr [left-end main-start] 1fr [main-end right-start] .6fr [right-end];

  @media screen and (max-width: 81.25em) {
    grid-template-columns: [left-start] .4fr [left-end main-start] 1fr [main-end];
  }

  @media screen and (max-width: 60em) {
    grid-template-columns: [left-start] 4rem [left-end main-start] 1fr [main-end];
  }
  grid-gap: 30px;
  align-items: start;
  max-width: 120rem;
  padding: 0px 4rem;
  margin: 0 auto;
}