.tweet-card {
  padding: 1rem 1rem;
  display: grid;
  grid-template-columns: 40px 1fr 20px;
  align-items: flex-start;
  grid-gap: .8rem;
  border-bottom: 1px solid rgb(235, 238, 240);
  &:hover {
    background-color: #f7f9fa;
    transition: .2s ease;
    cursor: pointer;
  }


  .tweet-img {
    width: 40px;
    height: 40px;

    img {
      width: 100%;
      height: 100%;
      border-radius: 100%;
    }
  }
  .tweet-body {
    color: #4e5255;
    .tweet-user {
      span {
        display: inline-block;
        margin-right: 4px;
        font-size: 1.1rem;
      }
      .tweet-username {
        font-weight: 700;
      }
      .tweet-handle {
        color: #80919f;
        font-size: 1rem;
      }
    }
    .tweet-content {
      font-size: 1.1rem;
    }
    .tweet-buttons {
      margin-top: 10px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .button-holder {
        display: flex;
        align-items: center;
        &:hover {

        }
        .icon-holder {
          border-radius: 100%;
          padding: 7px;
          display: flex;
          justify-content: center;
          align-items: center;
          margin-right: 4px;
          .button-icon {
            width: 1.2rem;
            height: 1.2rem;
          }
        }
        .icon-text {

        }
      }
    }
  }

  .more-icon {
    font-weight: 700;
    font-size: 1.3rem;
  }
}