.left-menu {
  grid-column: left-start / left-end;
  padding-top: .5rem;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 10px;
  align-content: flex-start;
  position: sticky;
  top: 0;
  // background-color: green;

  @media screen and (max-width: 60em) {
    .link,
    .btn-holder {
      display: none;
    }
  }

  .twitter-icon {
    width: 2rem;
    height: 2rem;
    margin-left: 1rem;

    .t-icon {
      width: 100%;
      height: 100%;
    }
  }
  .menu-links {
    .link-group {
      display: flex;
      flex-direction: column;
      .link-group-item {
        list-style: none;
        display: flex;
        align-items: center;
        border-radius: 10000px;
        font-weight: 700;
        padding: .7rem 1.2rem;
        align-self: flex-start;
        &:hover{
          background-color: #e8f5fe;
          cursor: pointer;
          transition: .2s ease;
          .link {
            color: #1da1f2;
          }
          .link-icon {
            .link-svg {
              fill: #1da1f2;
            }
          }
        }
        .link-icon {
          width: 1.5rem;
          height: 1.5rem;
          margin-right: 1.2rem;
          img {
            width: 100%;
          }
          .link-svg {
            width: 100%;
            fill: #1e2327;
            height: 100%;
          }
        }
        .link{
          font-size: 1.3rem;
          color: #1e2327;
          text-decoration: none;
          &:focus,
          &:visited {
            text-decoration: none;
          }
        }
      }
    }
  }
  .btn-holder {
    width: 100%;
    .btn {
      padding: 1rem;
      border-radius: 10000px;
      outline: none;
      border: none;
      width: 100%;
      color: #fff;
      background-color: #1da1f2;
      cursor: pointer;
      font-weight: 700;
      font-size: 1rem;
      &:hover {
        transition: .3s ease;
        background-color: #1a91da;
      }
    }
  }
  .profile-holder {
    width: 100%;
    @media screen and (max-width: 60em) {
      display: none;
    }
    display: flex;
    align-items: center;
    cursor: pointer;
    border-radius: 10000px;
    padding: .8rem;
    margin-top: 4rem;
    position: relative;
    & >:not(:last-child) {
      margin-right: .4rem;
    }
    &:hover {
      transition: .3s ease;
      background-color: #e8f5fe;
    }
    .profile-img {
      width: 40px;
      height: 40px;
      img {
        width: 100%;
        height: 100%;
        border-radius: 100%;
        object-fit: cover;
      }
    }
    .profile-details {
      flex: 1;
      width: 100%;
      display: flex;
      flex-direction: column;
      font-size: .9rem;
      white-space: nowrap;
      .profile-name {
        font-weight: 700;
        color: #1e2327;
      }
      .profile-handle {
        color: #80919f;
      }
    }
    .show-more {
      font-size: 1.4rem;
      align-self: flex-start;
      font-weight: 700;
    }

    .drop-down-menu {
      position: absolute;
      width: 24vw;
      bottom: 75px;
      left: -20px;
      display: flex;
      flex-direction: column;
      background-color: #fff;
      border-radius: 15px;
      padding: 10px 0;
      box-shadow: -3px 2px 18px -3px rgba(113,99,99,0.54);
      -webkit-box-shadow: -3px 2px 18px -3px rgba(113,99,99,0.54);
      -moz-box-shadow: -3px 2px 18px -3px rgba(113,99,99,0.54);
      .account {
        width: 100%;
        display: flex;
        align-items: center;
        cursor: pointer;
        border-radius: 10000px;
        padding: .8rem;
        position: relative;
        & >:not(:last-child) {
          margin-right: .4rem;
        }
        &:hover {
          transition: .3s ease;
          background-color: #e8f5fe;
        }
        .account-img {
          width: 40px;
          height: 40px;
          img {
            width: 100%;
            height: 100%;
            border-radius: 100%;
            object-fit: cover;
          }
        }
        .account-details {
          flex: 1;
          width: 100%;
          display: flex;
          flex-direction: column;
          font-size: .9rem;
          white-space: nowrap;
          .account-name {
            font-weight: 700;
            color: #1e2327;
          }
          .account-handle {
            color: #80919f;
          }
        }
        .account-show-more {
          font-size: 1.4rem;
          align-self: flex-start;
          font-weight: 700;
        }
      }

      .list {
        display: flex;
        flex-direction: column;
        .list-group {
          list-style: none;
          padding: 1rem;
          font-size: .9rem;
          border-bottom: 1px solid #eee;
          &:hover {
            background-color: #f7f9fa;
          }
        }
      }
    }
  }

}