.tweet-box {
  display: grid;
  grid-template-columns: 60px 1fr;
  background-color: #fff;
  // grid-gap: 1px;
  padding: .8rem;
  border-bottom: 1px solid rgb(235, 238, 240);
  align-items: flex-start;
  .profile-img {
    grid-column: 1 / 2;
    width: 40px;
    img {
      width: 100%;
      height: 100%;
      border-radius: 100%;
    }
  }
  .text {
    grid-column: 2 / -1;
    display: flex;
    flex-direction: column;
    .text-area {
      border-bottom: 1px solid rgb(235, 238, 240);
      margin-bottom: .8rem;
      .textarea {
        resize: none;
        outline: none;
        width: 100%;
        padding: .5rem 0;
        color: #8998a6;
        font-size: 1.2rem;
        font-weight: 600;
        border: none;
      }
    }
    .tweet-icons {
      align-self: flex-end;
      button {
        padding: .5rem 1rem;
        color: #fff;
        border-radius: 10000px;
        outline: none;
        border: none;
        background-color: #1da1f2;
        cursor: pointer;
        font-weight: 700;
        font-size: 1rem;
        &:hover {
          transition: .3s ease;
          background-color: #1a91da;
        }
      }
    }
  }
}